import React from "react";
import "../css/Project.css";
import projectThumbnail from "../image/KakaoTalk_20241211_103756459.png";
import projectThumbnail2 from "../image/UnrealEngine.svg";
import video1 from "../image/video/Waktron.mp4"; //  
import Modal from "./Modal";
import useModal from "./useModal";

function Projects() {
  const {
    isModalOpen,
    modalTitle,
    modalMessage,
    modalActions,
    openModal,
    closeModal,
  } = useModal();

  const projectData = [
    {
      title: "  &",
      duration: "2024.11.13 ~ 2024.12.26",
      members: "4 (  BACKEND )",
      roles: [
        "     ",
        "CRUD API  ",
        "GITHUB  ",
        "AWS ",
        "     ",
      ],
      thumbnail: projectThumbnail,
      links: [
        { url: "http://www.nayanong.site", text: " " },
        {
          url: "https://docs.google.com/spreadsheets/d/1cfhPAhxgoSSTt0Mr_6eZa3Z4JSX3FRCs0_GRCnQxJ0w/edit?gid=0#gid=0",
          text: "  ",
        },
        {
          url: "https://docs.google.com/presentation/d/1BV3QbqTImwcl4Vcj4pRKuG88QDvZNodp/edit?usp=drive_link&ouid=110274912202184154568&rtpof=true&sd=true",
          text: "()",
        },
        {
          url: "https://github.com/rambe98/naya-nong/issues",
          text: "GITHUB  ",
        },
      ],
    },
    {
      title: "3D Unreal Project",
      duration: "2022.9.1 ~ 2022.12.30",
      members: "3 (  blueprint  )",
      roles: [
        "  ",
        "  ",
        "  ",
        "     ",
      ],
      thumbnail: projectThumbnail2,
      links: [
        { url: "https://github.com/rambe98/WakTron.main", text: " Github " },
        
      ],
      video: video1, //   
    },
  ];

  const handleVideoClick = (videoUrl) => {
    openModal({
      title: " ",
      message: (
        <>
                <video controls autoPlay style={{ width: "100%" }}>
          <source src={videoUrl} type="video/mp4" />
             .
        </video>
        </>

      ),
      actions: [{ label: "", onClick: closeModal }],
    });
  };

  return (
    <section id="projects">
      <h2>PROJECTS</h2>
      <div className="projects-container">
        {projectData.map((project, index) => (
          <article key={index}>
            <div className="image-text">
              <img src={project.thumbnail} alt={`project-thumbnail-${index}`} />
              <p>{project.title}</p>
            </div>
            <div className="project-details">
              <h3> </h3>
              <p>: {project.duration}</p>
              <p>: {project.members}</p>
              <p>:</p>
              <ul>
                {project.roles.map((role, roleIndex) => (
                  <li key={roleIndex}>{role}</li>
                ))}
              </ul>
            </div>
            <div className="project-links">
            {project.links &&
                project.links.map((link, linkIndex) => (
                  <a
                    key={linkIndex}
                    href={link.url}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {link.text}
                  </a>
                ))}
              {project.video && (
                <button onClick={() => handleVideoClick(project.video)}>
                    
                </button>
              )}
            </div>
          </article>
        ))}
      </div>

      {/*   */}
      {isModalOpen && (
        <Modal
          title={modalTitle}
          content={modalMessage}
          actions={modalActions}
          onClose={closeModal}
        />
      )}
    </section>
  );
}

export default Projects;
